/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layouts
--------------------------------------------------------------*/
html {
	font-size: 16px;
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
header #wrapper__header-top {
	height: 8vh;
	display: flex;
	flex-direction: row;
}

header #wrapper__header-top div:first-of-type {
	background: #c23824;
	flex: 1 0 55vw;
}

header #wrapper__header-top div:nth-of-type(2) {
	background: #000000;
	flex: 1 0 15vw;
}

header #wrapper__header-top div:nth-of-type(3) {
	flex: 1 0 30vw;
	background: #394671;
}

header .site-title-wrapper,
header .site-description-wrapper {
	display: flex;
}

header .header-left, header .header-right {
	overflow-x: hidden;
}

header h1.site-title {
	font-size: 5em;
}

header p.site-title {
	font-size: 2.2em;
}

header h1.site-title, header p.site-title {
	font-family: Anton;
	display: inline-block;
	width: 100vw;
	padding: 0 2vw 0 5vw;
	margin: 0;
}

header h1.site-title a, header p.site-title a {
	letter-spacing: 2px;
	text-decoration: none;
	text-transform: lowercase;
}

header p.site-description {
	font-family: Cabin;
	font-size: 1.8em;
	padding-left: 3vw;
	background: #ece7e1;
	color: #394671;
	margin: 0 0 2% 0;
	padding-left: 5vw;
}

header .header-left {
	background: #c23824;
	flex: 1 0 70vw;
}

header .header-left h1.site-title, header .header-left p.site-title {
	color: #ece7e1;
}

header .header-left h1.site-title a, header .header-left p.site-title a {
	color: #ece7e1;
}

header .header-right {
	background: #ece7e1;
	flex: 1 0 30vw;
}

header .header-right h1.site-title, header .header-right p.site-title {
	color: #394671;
	position: relative;
	left: -70vw;
}

header .header-right h1.site-title a, header .header-right p.site-title a {
	color: #394671;
}

.main-navigation {
	background: black;
	clear: both;
	display: block;
	float: left;
	width: 100%;
}

.main-navigation a {
	color: #ece7e1;
	font-family: Cabin;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
}

.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}

.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}

.main-navigation li {
	float: left;
	position: relative;
}

.main-navigation a {
	display: block;
	text-decoration: none;
	padding: 4px 3vw;
	text-transform: lowercase;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

/*--------------------------------------------------------------
## Site Content (#content .site-content div)
--------------------------------------------------------------*/
div.site-content {
	width: 90%;
	margin: 3% auto;
	background: white;
}

/*--------------------------------------------------------------
## Background Wrapper (fills .site-content)
--------------------------------------------------------------*/
#content--background--outer-wrapper {
	position: relative;
	overflow: hidden;
	bottom: 0;
	width: 100%;
	/*    padding-top: 5%;*/
}

#content--background--outer-wrapper #content--background--inner-wrapper {
	position: absolute;
	z-index: -100;
	display: flex;
	flex-direction: row;
	/*        top: -10%;*/
	/*        height: 110%;*/
	height: 100%;
}

#content--background--outer-wrapper #content--background--inner-wrapper .content--background-primary {
	z-index: -10;
	background: #c23824;
	flex: 1 0 70vw;
}

#content--background--outer-wrapper #content--background--inner-wrapper .content--background-secondary {
	z-index: -10;
	background: #ece7e1;
	flex: 1 0 30vw;
	overflow-x: visible;
}

#content--background--outer-wrapper #content--background--inner-wrapper .content--background-secondary div {
	height: 100%;
	width: 45vw;
	position: relative;
	left: -15vw;
	/* background: lightgray; */
	background-image: url("data:image/svg+xml;base64,");
	background-size: auto .5rem;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.footer--background-wrapper {
	width: 100vw;
	display: flex;
	flex-direction: row;
}

.footer--background-wrapper div:first-of-type {
	background: #c23824;
	flex: 1 0 55vw;
}

.footer--background-wrapper div:first-of-type div {
	display: inline-block;
	padding-left: 5vw;
}

.footer--background-wrapper div:first-of-type div, .footer--background-wrapper div:first-of-type a {
	color: black;
	font-weight: bold;
}

.footer--background-wrapper div:first-of-type a {
	text-decoration: none;
}

.footer--background-wrapper div:first-of-type a:hover, .footer--background-wrapper div:first-of-type a:focus {
	text-decoration: underline;
}

.footer--background-wrapper div:nth-of-type(2) {
	background: #000000;
	flex: 1 0 15vw;
}

.footer--background-wrapper div:nth-of-type(3) {
	height: 30vh;
	flex: 1 0 30vw;
	background: #394671;
	color: #ece7e1;
}

.footer--background-wrapper div:nth-of-type(3) a {
	color: #ece7e1;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}
