#content--background--outer-wrapper {
    position: relative;
    overflow: hidden;
    bottom: 0;
    width: 100%;
/*    padding-top: 5%;*/
    
    #content--background--inner-wrapper {
        position: absolute;
        z-index: -100;
        display: flex;
        flex-direction: row;
/*        top: -10%;*/
/*        height: 110%;*/
        height: 100%;

        .content--background-primary {
            z-index: -10;
            background: $color__background-primary;
            flex: 1 0 70vw;
        }
    
        .content--background-secondary {
            z-index: -10;
            background: $color__background-secondary;
            flex: 1 0 30vw;
            overflow-x: visible;
            
            div {
                height: 100%;
                width: 45vw;
                position: relative;
                left: -15vw;


                /* background: lightgray; */
                background-image:   url("data:image/svg+xml;base64,");
                background-size: auto .5rem;

              }
        }
        
    }
    
    
    
    
    
}

